import React from "react";
import { useEffect, useState } from "react";
import styled, { createGlobalStyle } from "styled-components";
import $ from "jquery";
import ScrollMagic from "scrollmagic";
import { useWindowSize } from "./WindowSize";

const GlobalStyle = createGlobalStyle`
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;
}
body
{
overflow: hidden; /* Hide scrollbars */ 
 background-color: #ffffff;
}
.container::-webkit-scrollbar {
width: 0;  /* Remove scrollbar space */
background: transparent;  /* Optional: just make scrollbar invisible */
}
/* Optional: show position indicator in red */
.container::-webkit-scrollbar-thumb {
background: #FF0000;
}
a{
text-decoration: none;
color: inherit;
}
/*Loader*/

.loader-div {
width: 100%;
height: 100%;
position: fixed;
background:#031222;
text-align: center;
z-index: 999;


}

.loader-div h1 {
margin: 0;
padding: 0;

font-weight: 300;
font-size: 4vw;
color:#E4B464;
position: absolute;
top: 40%;
left: 50%;
transform: translate(-50%, -50%);
}

.loader-div h1 span {
position: relative;
top: 0.63em;  
display: inline-block;
text-transform: uppercase;  
opacity: 0;
transform: rotateX(-90deg);

}

.loader-div h1  .let1 {
animation: drop 1.2s ease-in-out infinite;
animation-delay: 1.2s;
}

.loader-div h1  .let2 {
animation: drop 1.2s ease-in-out infinite;
animation-delay: 1.3s;
}

.loader-div h1  .let3 {
animation: drop 1.2s ease-in-out infinite;
animation-delay: 1.4s;
}

.loader-div h1  .let4 {
animation: drop 1.2s ease-in-out infinite;
animation-delay: 1.5s;

}

.loader-div h1  .let5 {
animation: drop 1.2s ease-in-out infinite;
animation-delay: 1.6s;
}

.loader-div h1  .let6 {
animation: drop 1.2s ease-in-out infinite;
animation-delay: 1.7s;
}

.loader-div h1 .let7 {
animation: drop 1.2s ease-in-out infinite;
animation-delay: 1.8s;
}

@keyframes drop {
  10% {
      opacity: 0.5;
  }
  20% {
      opacity: 1;
      top: 3.78em;
      transform: rotateX(-360deg);
  }
  80% {
      opacity: 1;
      top: 3.78em;
      transform: rotateX(-360deg);
  }
  90% {
      opacity: 0.5;
  }
  100% {
      opacity: 0;
      top: 6.94em
  }
}


/**/
.effect-wrapper
{
 align-items: center;
 background-color: #000;
 display: flex;
 flex-shrink:0;
 flex-direction: column;
 perspective: 261.111vw;
 position: relative;
 width: 100%;
 
 overflow:hidden;
}
.effect
{
 overflow: hidden;
transform-origin: 100% 0;
transition: transform 300ms cubic-bezier(0.4, 0, 0.2, 1);
width: 100%;

}
.active  {
transform: rotateX(-35deg);

}
.overlay {
background: linear-gradient(#0000001f 0%, #0000009f 80%, #000000bf 100%);
height: 100%;
opacity: 0;
position: absolute;
transition: opacity 300ms cubic-bezier(0.4, 0, 0.2, 1);
top: 0;
width: 100%;
pointer-events: none;
}
.overlayactive {
opacity: 1;
}

.container-wrapper
{
width: 100%;
height: 100%;
display: flex;
flex-shrink: 0;
justify-content: center;
overflow:hidden;

}
.container
{
width: 90%;

overflow:scroll;
}

.image-container
{

width: 100%;
transform-origin: 50% 100%;
position: relative;
overflow:hidden;

}
.image
{
width: 100%;
height: 100%;
background-size: contain;
background-position: center;


}
h1,p{
color: white;
}
h1{

font-weight: 700;
word-break: break-word;
}


.first-name,.last-name
{

width: 90%;
  
font-family: Roboto;
font-style: normal;
font-weight: bold;
font-weight: 700;

}

.tag
{
width: 90%;		
font-family: Roboto;
font-style: normal;
font-weight: 900;		
letter-spacing: 0.1em;
color: #E9C49A;
 
}



 .contact-container
 {
     
     width: 100%;
     display: flex;
     flex-shrink: 0;
     align-items: center;
 }

 .contact-txt
 {
   
     font-weight: 300;
     
     width: 80%;

 }

 .fixed-buttons
 {
     width: 90%;
     
     position: fixed;
     top: auto;
     display: flex;
     flex-shrink: 0;
     justify-content: center;
     align-items: flex-end;
     top:auto;
     bottom: 1%;
     z-index: 1;
     overflow:hidden;
    
 }

 .share-icon-holder
 {
     display: flex;
     flex-shrink: 0;
     flex-direction: column;
     -webkit-box-pack: center;
     justify-content: center;
     -webkit-box-align: center;
     align-items: center;
     transition: 0.1s ease-out opacity;
 }

 

 .share-menu,.share-menu-barcode {
background: #06251B;
background-blend-mode: screen;
bottom: 0;
display: flex;
flex-shrink: 0;
flex-wrap: wrap;
height: 60vw;
position: absolute;
transform: translateY(60vw);
transition: transform 300ms cubic-bezier(0.4, 0, 0.2, 1);
width: 100%;

}



.open
{
transform: translateY(0vw);
}

 .heading-name-03
 {
    
     font-family: Roboto;
     font-style: normal;
     font-weight: bold;
     
     
 }

 .input
{
 outline: none; 
 box-shadow: none; 
 border: none ;
 font-family: Roboto;
 font-style: normal;

 background: #FFFFFF;
 /* Light Grey */
 
 box-sizing: border-box;
 border-radius: 50px;

 border-radius: 50px;

}

.form
{
    display: flex;
    flex-shrink: 0;
    flex-direction: column;
    width: 85%;
}

 .primary-btn
 {
    
     font-family: Roboto;
     font-style: normal;
     font-weight: bold;
    
     color: #FFFFFF;;
     background-color: #34413B;
     border-radius: 50px;
     background: #E4B464;
     box-shadow: 0px 4px 20px rgba(86, 84, 84, 0.15);
     background-size: cover;
     background-position: center;
     border: none;
     /*box-shadow: 0px 2px 4px 50px rgba(255, 223, 161, 0.1);*/
     display: inline-block;
     

    
 }

 .line
 {
     width: 90%;
     height: 1px;
     background-color: white;
    
 }


 /*share icons*/



 .share-slide
 {

  display: flex;
  flex-shrink: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  
 }
 .close-icon
 {
     position: absolute;
     top:10%;
     right: 7%;
     
 }
 .share-slide-share-icons
 {

     display: flex;
    
     flex-direction: column;
     align-items: center;
     flex-shrink: 0;
     position: fixed;
     top: 50%;
     left: 50%; 
     /* bring your own prefixes */
     transform: translate(-50%, -55%);
    
 }
 .share-headline 
 {
     color: #ffffff;  
 }
 .scan-and-save-txt
 {
    font-size: min(5vw,2.5vh);
    margin-bottom:  min(4vw,4vh);
     color: #1D3138;
     
     margin-top: 30%;
    
  
     
 }


 
 .icon-grid
 {
     display: grid;
     width: 100%;
     grid-template-columns: 11vw 11vw 11vw 11vw;
     justify-content: center;

 }
 .icon-grid a
 {
     display: flex;
     justify-content: center;
     align-items: center;
     flex-shrink: 0;
    
 }

 .qr-code-wrapper
 {
     width: 100%;
     height: 50vh;
     display: flex;
     flex-direction: column;
     flex-shrink: 0;
     color: #1D4B7A;
     align-items: center;
 }
 .qr-code-container
 {
     
  
     transition:  transform 1s ease-out;
 }
 .image{
    animation: zoomLoop 10s infinite;
 }
 @keyframes zoomLoop {
    0% {

      transform: scale(1);
      
    }
    50%{
      transform: scale(1.1);
    }
    
    100% {
    transform: scale(1);

    }
  }

  .arrow {
		position: absolute;
    text-align: center;
    right: 3vw;
    opacity: 0;
    
	}
     .arrow img{
    
      margin-top: 7.299vw;
      margin-left: 1.217vw;
      filter: invert(0%);
	}
.bounce 
    {
      transition: 0.2s ease-out;
      opacity: 1;
  		animation: bounce 2s infinite;
	}
  .bounceBack 
  {
    transition: 0.2s ease-out;
    animation: bounceBack 2s infinite;
}
.bounceBack img
{
  transform: rotate(-180deg);
}

.bounce1
{
  opacity: 0;
}
.bounce2
{
  opacity: 1;
}
        
        



@keyframes bounce 
{
    
  0%, 20%, 50%, 80%, 100% 
	{
    transform: translateY(0);
    }
  40% 
   {
    transform: translateY(-7.299vw);
   }
  60% 
   {
    transform: translateY(-3.65vw);
   }
} 

@keyframes bounceBack 
{
    
  0%, 20%, 50%, 80%, 100% 
	{
    transform: translateY(0);
    }
  40% 
   {
    transform: translateY(7.299vw);
   }
  60% 
   {
    transform: translateY(3.65vw);
   }
}






`;

const Lawyer = () => {
  const size = useWindowSize();

  const getSize = (width, heightPercentage) => {
    return `min(${width}vw, ${(size.height * heightPercentage) / 100}px)`;
  };
  const getHeight = (heightPercentage) => {
    return `${(size.height * heightPercentage) / 100}px`;
  };

  const [darkMode, setDarkMode] = useState(false);
  const [image, setImage] = useState(
    "https://raw.githubusercontent.com/rohit-virkud/lawyer/main/img.jpg"
  );
  const [firstName, setFirstName] = useState("George");
  const [lastName, setLastName] = useState("Mask");
  const [email, setEmail] = useState("info@artcoolcards.gr");
  const [address, setAddress] = useState(
    "Sr no:14/15, vitthal nagar colony , near sai ganesh apt, sinhagad road, pune - 51"
  );
  const [mobile, setMobile] = useState("+91 8888306063");
  const [landline, setLandline] = useState("+91 8888306063");
  const [website, setWebsite] = useState("www.artcoolcards.com");
  const [profession, setProfession] = useState("LAWYER");
  const [followTitle, setFollowTitle] = useState("Follow Us");
  const [reviewTitle, setReviewTitle] = useState("Our Reviews");
  const [subTitle, setSubTitle] = useState("Subscribe");

  const [facebook, setFacebook] = useState("https://artcoolcards.gr/");
  const [instagram, setInstagram] = useState("https://artcoolcards.gr/");
  const [linkedin, setLinkedin] = useState("https://artcoolcards.gr/");
  const [youtube, setYoutube] = useState("https://artcoolcards.gr/");
  const [twitter, setTwitter] = useState("https://artcoolcards.gr/");
  const [tiktok, setTiktok] = useState("https://artcoolcards.gr/");
  const [telegram, setTelegram] = useState("https://artcoolcards.gr/");

  const [tripadvisor, setTripadvisor] = useState("https://artcoolcards.gr/");
  const [booking, setBooking] = useState("https://artcoolcards.gr/");

  const [enableFacebook, setEnableFacebook] = useState(true);
  const [enableYoutube, setEnableYoutube] = useState(true);
  const [enableTelegram, setEnableTelegram] = useState(true);
  const [enableTwitter, setEnableTwitter] = useState(true);
  const [enableTiktok, setEnableTiktok] = useState(true);
  const [enableInstagram, setEnableInstagram] = useState(true);
  const [enableLinkedin, setEnableLinkedin] = useState(true);

  const [enableBooking, setEnableBooking] = useState(true);
  const [enableTripadvisor, setEnableTripadvisor] = useState(true);

  const [enableEmail, setEnableEmail] = useState(true);
  const [enableAddress, setEnableAddress] = useState(true);
  const [enableMobile, setEnableMobile] = useState(true);
  const [enableLandline, setEnableLandline] = useState(true);
  const [enableWebsite, setEnableWebsite] = useState(true);
  const [enableReviewName, setEnableSubsribe] = useState(true);
  const [enableFollowName, setEnableFollow] = useState(true);

  const [enableReviews, setEnableReviews] = useState(true);
  const [enableSocial, setEnableSocial] = useState(true);
  const [enableProfession, setEnableProfession] = useState(true);

  useEffect(() => {
    //eslint-disable-next

    setTimeout(function () {
      $(".loader-div span").fadeOut("fast");

      $(".loader-div").fadeOut();
    }, 2000);

    const clamp = (x, min, max) => Math.max(min, Math.min(x, max));
    const list = document.querySelector(".container");
    const card = document.querySelector(".image-container");
    list.addEventListener("scroll", (e) => {
      const scale = clamp((200 - list.scrollTop + 0.3 * 72) / 100, 0, 1);
      const opacity = clamp((160 - list.scrollTop + 0.3 * 72) / 60, 0, 1);
      card.style.transform = `scale(${scale})`;
      card.style.opacity = `${opacity}`;
    });
  }, []);

  const controller = new ScrollMagic.Controller();

  const scene = new ScrollMagic.Scene({
    triggerElement: ".line",
  })
    .setClassToggle(".bounce", "bounce1")
    .addTo(controller);

  const scene2 = new ScrollMagic.Scene({
    triggerElement: ".line",
  })
    .setClassToggle(".bounceBack", "bounce2")
    .addTo(controller);

  function openShare() {
    $(".effect").toggleClass("active");
    $(".overlay").css("opacity", "1");
    $(".share-icon-holder").css("opacity", "0");
    $(".share-menu").toggleClass("open");
  }

  function closeShare() {
    $(".effect").toggleClass("active");
    $(".overlay").css("opacity", "0");
    $(".share-icon-holder").css("opacity", "1");
    $(".share-menu").toggleClass("open");
  }

  function openBarcode() {
    $(".effect").toggleClass("active");
    $(".overlay").css("opacity", "1");
    $(".share-icon-holder").css("opacity", "0");
    $(".share-menu-barcode").toggleClass("open");
  }
  function closeBarcode() {
    $(".effect").toggleClass("active");
    $(".overlay").css("opacity", "0");
    $(".share-icon-holder").css("opacity", "1");
    $(".share-menu-barcode").removeClass("open");
  }

  return (
    <div style={{ overflow: "hidden", position: "relative" }}>
      <GlobalStyle />

      <div
        className="loader-div"
        style={{
          backgroundColor: darkMode ? "#092F24" : "#ffffff",
        }}
      >
        <h1
          style={{
            color: darkMode ? "#E4B464" : "#000000",
          }}
        >
          <span className="let1">l</span>
          <span className="let2">o</span>
          <span className="let3">a</span>
          <span className="let4">d</span>
          <span className="let5">i</span>
          <span className="let6">n</span>
          <span className="let7">g</span>
        </h1>
      </div>

      <div
        className="effect-wrapper"
        style={{
          height: getHeight(100),
        }}
      >
        <div
          className="effect"
          style={{
            height: getHeight(100),
            background: darkMode
              ? "linear-gradient(179.7deg,#0C201A 0.4%,#05130F 34.65%,rgba(1,31,22,0.98) 70.18%,rgba(0,40,28,0.96) 96.64%)"
              : "linear-gradient(193.22deg, #FFFFFF 34.11%, #E7DFD5 113.1%)",
          }}
        >
          <div
            className="arrow bounce"
            style={{
              top: getSize(150, 90),
              fontSize: getSize(2.433, 1.2),
            }}
          >
            <p
              style={{
                transform: "rotate(-90deg)",
                color: darkMode ? "#fff" : "#000",
                marginLeft: getSize(1, 0.5),
              }}
            >
              Scroll Down
            </p>
            <img
              src={
                darkMode
                  ? "https://raw.githubusercontent.com/rohit-virkud/lawyer/main/arrow.png"
                  : "https://raw.githubusercontent.com/rohit-virkud/lawyer/main/arrow-dark.png"
              }
              alt="arrow-img"
              style={{
                marginTop: "7.299vw",
                marginLeft: "1.217vw",
                width: getSize(2.5, 1.2),
              }}
            />
          </div>

          <div
            className="arrow bounceBack"
            style={{
              fontSize: getSize(2.433, 1.2),
              top: "80%",
            }}
          >
            <img
              src={
                darkMode
                  ? "https://raw.githubusercontent.com/rohit-virkud/lawyer/main/arrow.png"
                  : "https://raw.githubusercontent.com/rohit-virkud/lawyer/main/arrow-dark.png"
              }
              alt="arrow-img"
              style={{
                marginBottom: "5vw",
                marginLeft: "1.217vw",

                width: getSize(2.5, 1.2),
              }}
            />
            <p
              style={{
                transform: "rotate(-90deg)",
                color: darkMode ? "#fff" : "#000",

                marginLeft: getSize(1.5, 0.8),
              }}
            >
              Scroll Up
            </p>
          </div>

          <div className="container-wrapper">
            <div
              className="container"
              style={{
                height: getHeight(88),
              }}
            >
              <div
                className="image-container"
                style={{
                  marginTop: getSize(5, 3),
                  borderRadius: getSize(5, 3),
                  height: getHeight(25),
                }}
              >
                <img src={image} alt="image" className="image" />
              </div>

              <div className="slide-01-wrapper">
                <div
                  className="content-margin"
                  style={{
                    marginLeft: getSize(3, 1.5),
                    minHeight: getHeight(60),
                  }}
                >
                  <h1
                    className="first-name"
                    style={{
                      marginTop: getSize(5, 2.5),
                      fontSize: getSize(7.5, 3.5),
                      lineHeight: getSize(6, 4),
                      color: darkMode ? "#fff" : "#000",
                    }}
                  >
                    {firstName}
                  </h1>
                  <h1
                    className="last-name"
                    style={{
                      fontSize: getSize(7.5, 3.5),
                      lineHeight: getSize(6, 4),
                      color: darkMode ? "#fff" : "#000",
                    }}
                  >
                    {lastName}
                  </h1>
                  {enableProfession && (
                    <p
                      className="tag"
                      style={{
                        fontSize: getSize(3.2, 1.6),
                        marginTop: getSize(2, 1),
                        color: darkMode ? "#E9C49A" : "#888888",
                      }}
                    >
                      {profession}
                    </p>
                  )}

                  <div
                    className="contact-container-wrapper"
                    style={{
                      marginTop: getSize(10.291, 5.5),
                    }}
                  >
                    {enableMobile && (
                      <a
                        href={`tel:${mobile}`}
                        className="contact-container cc-top"
                      >
                        <img
                          src={
                            darkMode
                              ? "https://raw.githubusercontent.com/rohit-virkud/lawyer/main/phone-icon.png"
                              : "https://raw.githubusercontent.com/rohit-virkud/lawyer/main/phone-icon-dark.png"
                          }
                          alt="phone"
                          className="contact-icon"
                          style={{
                            width: getSize(8, 5),
                            height: getSize(8, 5),
                          }}
                        />
                        <p
                          className="contact-txt"
                          style={{
                            marginLeft: getSize(4.116, 2.5),
                            fontSize: getSize(4, 2),
                            lineHeight: getSize(5, 2.5),
                            color: darkMode ? "#fff" : "#000",
                          }}
                        >
                          +91 8888306064
                        </p>
                      </a>
                    )}

                    {enableLandline && (
                      <a
                        href={`tel:${landline}`}
                        className="contact-container"
                        style={{
                          marginTop: getSize(5.569, 3.2),
                        }}
                      >
                        <img
                          src={
                            darkMode
                              ? "https://raw.githubusercontent.com/rohit-virkud/lawyer/main/home-icon.png"
                              : "https://raw.githubusercontent.com/rohit-virkud/lawyer/main/home-icon-dark.png"
                          }
                          alt="phone"
                          className="contact-icon"
                          style={{
                            width: getSize(8, 5),
                            height: getSize(8, 5),
                          }}
                        />
                        <p
                          className="contact-txt"
                          style={{
                            marginLeft: getSize(4.116, 2.5),
                            fontSize: getSize(4, 2),
                            lineHeight: getSize(5, 2.5),
                            color: darkMode ? "#fff" : "#000",
                          }}
                        >
                          {landline}
                        </p>
                      </a>
                    )}

                    {enableEmail && (
                      <a
                        href={`mailto:${email}`}
                        className="contact-container"
                        style={{
                          marginTop: getSize(5.569, 3.2),
                        }}
                      >
                        <img
                          src={
                            darkMode
                              ? "https://raw.githubusercontent.com/rohit-virkud/lawyer/main/msg-icon.png"
                              : "https://raw.githubusercontent.com/rohit-virkud/lawyer/main/msg-icon-dark.png"
                          }
                          alt="phone"
                          className="contact-icon"
                          style={{
                            width: getSize(8, 5),
                            height: getSize(8, 5),
                          }}
                        />
                        <p
                          className="contact-txt"
                          style={{
                            marginLeft: getSize(4.116, 2.5),
                            fontSize: getSize(4, 2),
                            lineHeight: getSize(5, 2.5),
                            color: darkMode ? "#fff" : "#000",
                          }}
                        >
                          {email}
                        </p>
                      </a>
                    )}

                    {enableAddress && (
                      <a
                        href={`https://maps.google.com/?q=${address}`}
                        target="_blank"
                        rel="noopener noreferrer"
                        className="contact-container"
                        style={{
                          marginTop: getSize(5.569, 3.2),
                        }}
                      >
                        <img
                          src={
                            darkMode
                              ? "https://raw.githubusercontent.com/rohit-virkud/lawyer/main/location-icon.png"
                              : "https://raw.githubusercontent.com/rohit-virkud/lawyer/main/location-icon-dark.png"
                          }
                          alt="phone"
                          className="contact-icon"
                          style={{
                            width: getSize(8, 5),
                            height: getSize(8, 5),
                          }}
                        />
                        <p
                          className="contact-txt"
                          style={{
                            marginLeft: getSize(4.116, 2.5),
                            fontSize: getSize(4, 2),
                            lineHeight: getSize(5, 2.5),
                            color: darkMode ? "#fff" : "#000",
                          }}
                        >
                          {address}
                        </p>
                      </a>
                    )}

                    {enableWebsite && (
                      <a
                        href={website}
                        target="_blank"
                        rel="noopener noreferrer"
                        className="contact-container"
                        style={{
                          marginTop: getSize(5.569, 3.2),
                          marginBottom: getSize(12.107, 6.5),
                        }}
                      >
                        <img
                          src={
                            darkMode
                              ? "https://raw.githubusercontent.com/rohit-virkud/lawyer/main/web-icon.png"
                              : "https://raw.githubusercontent.com/rohit-virkud/lawyer/main/web-icon-dark.png"
                          }
                          alt="phone"
                          className="contact-icon"
                          style={{
                            width: getSize(8, 5),
                            height: getSize(8, 5),
                          }}
                        />
                        <p
                          className="contact-txt"
                          style={{
                            marginLeft: getSize(4.116, 2.5),
                            fontSize: getSize(4, 2),
                            lineHeight: getSize(5, 2.5),
                            color: darkMode ? "#fff" : "#000",
                          }}
                        >
                          {website}
                        </p>
                      </a>
                    )}
                  </div>
                </div>

                <div
                  className="line"
                  style={{
                    marginBottom: getSize(12.107, 6.5),
                    background: darkMode ? "#fff" : "#000",
                  }}
                ></div>

                <div
                  className="container-03 socialmax"
                  style={{ marginTop: getSize(8, 4) }}
                >
                  {enableSocial && (
                    <div
                      class="social"
                      style={{
                        marginLeft: getSize(3, 1.5),
                      }}
                    >
                      {enableFollowName && (
                        <h1
                          className="heading-name-03"
                          style={{
                            marginTop: getSize(8, 4),
                            fontSize: getSize(5, 3),
                            lineHeight: getSize(6, 4.2),
                            color: darkMode ? "#fff" : "#000",
                          }}
                        >
                          {followTitle}
                        </h1>
                      )}

                      <div
                        className="social-icons"
                        style={{
                          marginTop: getSize(4, 2.5),
                        }}
                      >
                        {enableFacebook && (
                          <a
                            href={facebook}
                            target="_blank"
                            rel="noopener noreferrer"
                          >
                            <img
                              src="https://raw.githubusercontent.com/rohit-virkud/lawyer/main/fb.png"
                              alt="fb"
                              style={{
                                width: getSize(8, 5),
                                height: getSize(8, 5),
                              }}
                            />
                          </a>
                        )}

                        {enableInstagram && (
                          <a
                            href={instagram}
                            target="_blank"
                            rel="noopener noreferrer"
                            className="mg-lt"
                            style={{
                              marginLeft: getSize(2, 1),
                            }}
                          >
                            <img
                              src="https://raw.githubusercontent.com/rohit-virkud/lawyer/main/insta.png"
                              alt="insta"
                              style={{
                                width: getSize(8, 5),
                                height: getSize(8, 5),
                              }}
                            />
                          </a>
                        )}

                        {enableYoutube && (
                          <a
                            href={youtube}
                            target="_blank"
                            rel="noopener noreferrer"
                            className="mg-lt"
                            style={{
                              marginLeft: getSize(2, 1),
                            }}
                          >
                            <img
                              src="https://raw.githubusercontent.com/rohit-virkud/lawyer/main/youtube.png"
                              alt="youtube"
                              style={{
                                width: getSize(8, 5),
                                height: getSize(8, 5),
                              }}
                            />
                          </a>
                        )}

                        {enableTelegram && (
                          <a
                            href={telegram}
                            target="_blank"
                            rel="noopener noreferrer"
                            className="mg-lt"
                            style={{
                              marginLeft: getSize(2, 1),
                            }}
                          >
                            <img
                              src="https://raw.githubusercontent.com/rohit-virkud/lawyer/main/telegram.png"
                              alt="telegram"
                              style={{
                                width: getSize(8, 5),
                                height: getSize(8, 5),
                              }}
                            />
                          </a>
                        )}

                        {enableTwitter && (
                          <a
                            href={twitter}
                            target="_blank"
                            rel="noopener noreferrer"
                            className="mg-lt"
                            style={{
                              marginLeft: getSize(2, 1),
                            }}
                          >
                            <img
                              src="https://raw.githubusercontent.com/rohit-virkud/lawyer/main/twitter.png"
                              alt="twitter"
                              style={{
                                width: getSize(8, 5),
                                height: getSize(8, 5),
                              }}
                            />
                          </a>
                        )}

                        {enableTiktok && (
                          <a
                            href={tiktok}
                            target="_blank"
                            rel="noopener noreferrer"
                            className="mg-lt"
                            style={{
                              marginLeft: getSize(2, 1),
                            }}
                          >
                            <img
                              src="https://raw.githubusercontent.com/rohit-virkud/lawyer/main/tiktok.png"
                              alt="tiktok"
                              style={{
                                width: getSize(8, 5),
                                height: getSize(8, 5),
                              }}
                            />
                          </a>
                        )}

                        {enableLinkedin && (
                          <a
                            href={linkedin}
                            target="_blank"
                            rel="noopener noreferrer"
                            className="mg-lt"
                            style={{
                              marginLeft: getSize(2, 1),
                            }}
                          >
                            <img
                              src="https://raw.githubusercontent.com/rohit-virkud/lawyer/main/linkedin.png"
                              alt="linkedin"
                              style={{
                                width: getSize(8, 5),
                                height: getSize(8, 5),
                              }}
                            />
                          </a>
                        )}
                      </div>
                    </div>
                  )}

                  {enableReviews && (
                    <div
                      class="reviews"
                      style={{
                        marginLeft: getSize(3, 1.5),
                      }}
                    >
                      {enableReviewName && (
                        <h1
                          className="heading-name-03"
                          style={{
                            marginTop: getSize(8, 4),
                            fontSize: getSize(5, 3),
                            lineHeight: getSize(6, 4.2),
                            color: darkMode ? "#fff" : "#000",
                          }}
                        >
                          {reviewTitle}
                        </h1>
                      )}
                      <div
                        className="social-icons"
                        style={{
                          marginTop: getSize(4, 2.5),
                        }}
                      >
                        {enableTripadvisor && (
                          <a
                            href={tripadvisor}
                            target="_blank"
                            rel="noopener noreferrer"
                          >
                            <img
                              src="https://raw.githubusercontent.com/rohit-virkud/lawyer/main/Tripadvisor.png"
                              alt="fb"
                              style={{
                                width: getSize(8, 5),
                                height: getSize(8, 5),
                              }}
                            />
                          </a>
                        )}

                        {enableBooking && (
                          <a
                            href={booking}
                            target="_blank"
                            rel="noopener noreferrer"
                            className="mg-lt"
                            style={{
                              marginLeft: getSize(2, 1),
                            }}
                          >
                            <img
                              src="https://raw.githubusercontent.com/rohit-virkud/lawyer/main/bookingcom.png"
                              alt="insta"
                              style={{
                                width: getSize(8, 5),
                                height: getSize(8, 5),
                              }}
                            />
                          </a>
                        )}
                      </div>
                    </div>
                  )}
                </div>

                <div
                  className="form-wrapper"
                  style={{
                    marginLeft: getSize(3, 1.5),
                  }}
                >
                  <h1
                    className="heading-name-03"
                    style={{
                      marginTop: getSize(12, 6),
                      fontSize: getSize(5, 3),
                      lineHeight: getSize(6, 4.2),
                      color: darkMode ? "#fff" : "#000",
                    }}
                  >
                    {subTitle}
                  </h1>
                  <form
                    method="post"
                    className="form"
                    style={{ marginTop: getSize(4, 3) }}
                  >
                    <input
                      type="name"
                      name="name"
                      placeholder="Enter Your Name"
                      className="input"
                      style={{
                        paddingTop: getSize(2.39, 1.5),
                        paddingBottom: getSize(2.39, 1.5),
                        paddingLeft: getSize(3.668, 1.8),
                        paddingRight: getSize(3.668, 1.8),
                        fontSize: getSize(3.405, 1.8),
                        lineHeight: getSize(5.085, 3),
                      }}
                    />
                    <input
                      type="email"
                      name="email"
                      placeholder="Enter Your Email"
                      className="input"
                      style={{
                        marginTop: getSize(4, 2),
                        paddingTop: getSize(2.39, 1.5),
                        paddingBottom: getSize(2.39, 1.5),
                        paddingLeft: getSize(3.668, 1.8),
                        paddingRight: getSize(3.668, 1.8),
                        fontSize: getSize(3.405, 1.8),
                        lineHeight: getSize(5.085, 3),
                      }}
                    />
                    <div className="button-wrapper">
                      <input
                        type="submit"
                        name="submit"
                        value="SUBMIT"
                        className="primary-btn"
                        style={{
                          marginTop: getSize(4, 2),
                          width: "30%",
                          marginLeft: getSize(1, 0.5),
                          color: "#ffffff",
                          marginBottom: getSize(10, 5),
                          border: "none",

                          paddingTop: getSize(2.39, 1.5),
                          paddingBottom: getSize(2.39, 1.5),
                          paddingLeft: getSize(4, 2),
                          paddingRight: getSize(4, 2),
                          fontSize: getSize(3.405, 1.8),
                          lineHeight: getSize(5.085, 3),
                        }}
                      />
                    </div>
                  </form>
                </div>
              </div>
            </div>

            <div
              className="fixed-buttons"
              style={{
                height: getHeight(12),
              }}
            >
              <a
                href="#!"
                className="share-icon-holder"
                onClick={(e) => {
                  e.preventDefault();
                  openShare();
                }}
                style={{
                  marginTop: getSize(1.6, 1),
                }}
              >
                <img
                  src={
                    darkMode
                      ? "https://raw.githubusercontent.com/rohit-virkud/lawyer/main/share-button.png"
                      : "https://raw.githubusercontent.com/rohit-virkud/lawyer/main/share-button-dark.png"
                  }
                  alt="share"
                  className="share-icon"
                  style={{
                    width: getSize(10, 6),
                    height: getSize(10, 6),
                  }}
                />

                <p
                  className="share-text"
                  style={{
                    fontSize: getSize(3, 1.5),
                    marginTop: getSize(2, 1.5),
                    color: darkMode ? "#fff" : "#000",
                  }}
                >
                  share
                </p>
              </a>

              <a href="#!" className="share-icon-holder">
                <img
                  src={
                    darkMode
                      ? "https://raw.githubusercontent.com/rohit-virkud/lawyer/main/save-btn.png"
                      : "https://raw.githubusercontent.com/rohit-virkud/lawyer/main/save-btn-dark.png"
                  }
                  alt="share"
                  className="save-icon"
                  style={{
                    width: getSize(12, 8),
                    height: getSize(12, 8),
                    marginLeft: getSize(10, 6),
                    marginRight: getSize(10, 6),
                  }}
                />
                <p
                  className="share-text"
                  style={{
                    fontSize: getSize(3, 1.5),
                    marginTop: getSize(2, 1.5),
                    color: darkMode ? "#fff" : "#000",
                  }}
                >
                  save contacts
                </p>
              </a>

              <a
                href="#!"
                className="share-icon-holder"
                onClick={(e) => {
                  e.preventDefault();
                  openBarcode();
                }}
                style={{
                  marginTop: getSize(1.8, 1),
                }}
              >
                <img
                  src={
                    darkMode
                      ? "https://raw.githubusercontent.com/rohit-virkud/lawyer/main/barcode.png"
                      : "https://raw.githubusercontent.com/rohit-virkud/lawyer/main/barcode-dark.png"
                  }
                  alt="share"
                  className="share-icon"
                  style={{
                    width: getSize(10, 6),
                    height: getSize(10, 6),
                  }}
                />
                <p
                  className="share-text"
                  style={{
                    fontSize: getSize(3, 1.5),
                    marginTop: getSize(2, 1.5),
                    color: darkMode ? "#fff" : "#000",
                  }}
                >
                  qr code
                </p>
              </a>
            </div>
          </div>
        </div>

        <div className="overlay"></div>
      </div>

      <div
        className="share-menu"
        style={{
          borderTopLeftRadius: getSize(5, 3),
          borderTopRightRadius: getSize(5, 3),
          background: darkMode ? "#06251B" : "#F0ECE6",
        }}
      >
        <a
          href="#!"
          onClick={(e) => {
            e.preventDefault();
            closeShare();
          }}
        >
          <img
            src={
              darkMode
                ? "https://raw.githubusercontent.com/rohit-virkud/lawyer/main/close-icon.png"
                : "https://raw.githubusercontent.com/rohit-virkud/lawyer/main/close-icon-dark.png"
            }
            alt="close-icon"
            className="close-icon"
            style={{
              width: getSize(5, 3),
              height: getSize(5, 3),
            }}
          />
        </a>

        <div className="share-slide-share-icons">
          <h2
            className="share-headline"
            style={{
              fontSize: getSize(5, 2.5),
              marginBottom: getSize(4, 3),
              color: darkMode ? "#fff" : "#000",
            }}
          >
            SHARE MY CARD
          </h2>
          <div
            className="icon-grid"
            style={{
              gridGap: getSize(2, 1),
            }}
          >
            <a href="#!">
              <img
                src="https://raw.githubusercontent.com/rohit-virkud/lawyer/main/sms.png"
                alt="sms-img"
                style={{
                  width: getSize(11, 5),
                  height: getSize(11, 5),
                }}
              />
            </a>
            <a href="#!">
              <img
                src="https://raw.githubusercontent.com/rohit-virkud/lawyer/main/cs.png"
                alt="cs-img"
                style={{
                  width: getSize(11, 5),
                  height: getSize(11, 5),
                }}
              />
            </a>
            <a href="#!">
              <img
                src="https://raw.githubusercontent.com/rohit-virkud/lawyer/main/whatsapp.png"
                alt="whatsapp-img"
                style={{
                  width: getSize(11, 5),
                  height: getSize(11, 5),
                }}
              />
            </a>
            <a href="#!">
              <img
                src="https://raw.githubusercontent.com/rohit-virkud/lawyer/main/dropbox 187.png"
                alt="dropbox-img"
                style={{
                  width: getSize(11, 5),
                  height: getSize(11, 5),
                }}
              />
            </a>
            <a href="#!" style={{ marginTop: getSize(2, 1) }}>
              <img
                src="https://raw.githubusercontent.com/rohit-virkud/lawyer/main/fb-share.png"
                alt="fb-img"
                style={{
                  width: getSize(11, 5),
                  height: getSize(11, 5),
                }}
              />
            </a>
            <a href="#!" style={{ marginTop: getSize(2, 1) }}>
              <img
                src="https://raw.githubusercontent.com/rohit-virkud/lawyer/main/telegram 191.png"
                alt="tel-img"
                style={{
                  width: getSize(11, 5),
                  height: getSize(11, 5),
                }}
              />
            </a>
            <a href="#!" style={{ marginTop: getSize(2, 1) }}>
              <img
                src="https://raw.githubusercontent.com/rohit-virkud/lawyer/main/twitter-share.png"
                alt="twit-img"
                style={{
                  width: getSize(11, 5),
                  height: getSize(11, 5),
                }}
              />
            </a>
            <a href="#!" style={{ marginTop: getSize(2, 1) }}>
              <img
                src="https://raw.githubusercontent.com/rohit-virkud/lawyer/main/linkedin-share.png"
                alt="linkedin-img"
                style={{
                  width: getSize(11, 5),
                  height: getSize(11, 5),
                }}
              />
            </a>
          </div>
        </div>
      </div>

      <div
        className="share-menu-barcode"
        style={{
          borderTopLeftRadius: getSize(5, 3),
          borderTopRightRadius: getSize(5, 3),
          background: darkMode ? "#06251B" : "#F0ECE6",
        }}
      >
        <a
          href="#!"
          onClick={(e) => {
            e.preventDefault();
            closeBarcode();
          }}
        >
          <img
            src={
              darkMode
                ? "https://raw.githubusercontent.com/rohit-virkud/lawyer/main/close-icon.png"
                : "https://raw.githubusercontent.com/rohit-virkud/lawyer/main/close-icon-dark.png"
            }
            alt="close-icon"
            className="close-icon"
            style={{
              width: getSize(5, 3),
              height: getSize(5, 3),
            }}
          />
        </a>

        <div className="share-slide-share-icons">
          <h2
            className="share-headline"
            style={{
              fontSize: getSize(5, 2.5),
              marginBottom: getSize(4, 3),
              color: darkMode ? "#fff" : "#000",
            }}
          >
            SCAN AND SHARE
          </h2>

          <div className="qr-code-container">
            <img
              src="https://raw.githubusercontent.com/rohit-virkud/lawyer/main/qr.png"
              alt="qr-img"
              style={{
                width: getSize(30, 20),
                height: getSize(30, 20),
              }}
            />
          </div>
        </div>
      </div>
    </div>
  );
};

export default Lawyer;
